import React from "react";
import { IoLogoLinkedin, IoMdMail, IoIosFlask } from "react-icons/io";
import SplitText from "react-pose-text";
import posed from "react-pose";
import { Header } from "./style";
import { motion } from "framer-motion";

const HeaderElement = () => {
  const ano = new Date().getFullYear();

  const Description = posed.div({
    exit: {
      opacity: 0
    },
    enter: {
      opacity: 1,
      beforeChildren: true,
      staggerChildren: 50,
      transition: { delay: 1500 }
    },
    transition: {
      delay: 5
    }
  });

  const charPoses = {
    exit: { opacity: 0 },
    enter: { opacity: 1 }
  };

  return (
    <Header>
      <motion.h1
        initial={{ opacity: 0, y: 50 }}
        transition={{ delay: 0.5, duration: 0.5 }}
        animate={{ opacity: 1, y: 0 }}
      >
        <strong>Olá</strong>, meu nome é <strong>Rodrigo Antunes</strong>
      </motion.h1>
      <motion.h3
        initial={{ opacity: 0 }}
        transition={{ delay: 1, duration: 0.5 }}
        animate={{ opacity: 1 }}
      >
        Desenvolvedor Front-End
      </motion.h3>

      <Description className="description" initialPose="exit" pose="enter">
        Trabalho há {ano - 2009} anos com Desenvolvimento Web. <br /> Gosto de
        criar experiências interativas com{" "}
        <SplitText charPoses={charPoses}>HTML5, CSS3 e Javascript.</SplitText>
      </Description>

      <motion.div 
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 3, duration: 0.5, delayChildren: 1.5 }}
        className="contato"
      >
        <ul>
          <li>
            <a
              rel="noopener noreferrer"
              title="Linkedin"
              target="_blank"
              href="https://www.linkedin.com/in/rodrigoantunespaiva/"
            >
              <IoLogoLinkedin />
            </a>
          </li>
          <li>
            <a href="mailto:rodrigoteajuda@gmail.com">
              <IoMdMail />
            </a>
          </li>
          {/* <li>
            <IoIosFlask />
          </li> */}
        </ul>
      </motion.div>
    </Header>
  );
};

export default HeaderElement;



